*{
  margin:0px;
  padding: 0px;
}

html{
  height: 100%;
}
body{
  height: 100%;
}

#root{
  width: 100%;
  height: 100%;
  
}

.App{
  background-color: #1e2021;
  color: white;
  width: 100%;
  height: 100%;
  min-height: 650px;
  display: flex;
}




.resultH1{
  font-family: 'Russo One', sans-serif;
  letter-spacing: 3px;
  margin-bottom: 10px;
  width: 350px;
    text-align: center;
}

.text{
  font-family: 'Russo One', sans-serif;
}

.colorOptions{
  width: auto;
  height: 50px;
}
.select{
  border-radius: 8px;
  color: white;
  background-color: #1e2021;
  margin-left: 10px;
}

.boardContainer{
  display: flex;
  flex-direction: column;
  align-items: center;

}

.none{
  display: none;
}

.settings{
  box-shadow: rgb(61 224 58) 0px 0px 5px 1px;
  border-radius: 10px;
  padding: 10px;
  margin-left: 10px;
  width: 310px;
}

.button{
  width: 100px;
  height: 30px;
  background-color: #3de03a;
  border-radius: 4px;
  box-shadow: 0px 0px 5px 3px #3de03a;
  border: none;
  margin-left: 10px;
  margin-top: 10px;
  font-family: 'Kanit', sans-serif;
  color: white;
}

.controlButton>img{
  width: 15px;
  height: 13px;
}

.controlButtons{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 30px;
    margin-top: 15px;
}

.controlButtons>div{
  background-color: #3de03a;
  height: 23px;
  width: 40px;
  margin: 5px;
  border-radius: 4px;
  box-shadow: 0px 0px 4px 3px #3de03a;
  font-family: 'Kanit', sans-serif;
}

.controlButton:nth-child(2){
  grid-column-start: 1;
  grid-column-end: 2;
  text-align: center;
}

.controlButton:nth-child(1){
  grid-column-start: 2;
  grid-column-end: 3;
  text-align: center;
}

.controlButton:nth-child(3){
  text-align: center;
}

.controlButton:nth-child(4){
  text-align: center;
}


@media screen and (max-width : 680px) {
  .App{
    display: flex;
    flex-direction: column;
    min-height: 650px;
  }

  .boardContainer {
      
      align-items: flex-start;
      margin-top: 25px;
      align-items: center;
  }

  

  .settingsContainer {
      display: flex;
      flex-direction: column;
      align-items: center;
  }
  .settings{
    margin-left: 0px;
  }

  .button{
    margin-left: 0px;
    margin-top: 20px;
  }


}
